<template>
  <div id="tablet">
    <div class="queryBox">
      <span class="title"><i class="iconfont icon-shenhe1"></i>人类活动变化检测详情</span>
      <Button type="primary" size="small" class="sign-btn" @click="$emit('signOut',false)">返回</Button>
    </div>
    <div class="map-box">
      <div class="topLeft-box" :class="{activeBox: boxstate === 1}">
        <Button :icon="boxstate === 1 ? 'md-contract' : 'md-expand'" @click="mapOpen" size="small" class="boxBtn"></Button>
        <Map :boxstate="boxstate" ref="maps" @layerConClick="layerConClick"></Map>
      </div>
      <div class="topRight-box" :class="{smallToBig:boxstate === 2}">
        <Button :icon="boxstate === 2 ? 'md-contract' : 'md-expand'" @click="boxstate = boxstate === 2 ? 0 :2" size="small" class="boxBtn"></Button>
        <div class="bigBox">
          <div class="smallBox">
            <Table border stripe :loading="mapLoading" :columns="mapColumns1" :data="mapData1" highlight-row>
            </Table>
          </div>
        </div>
      </div>
      <Card style="width: calc(100% - 30px)" class="bottom-box" id="changeDetect_mapBox" :class="{smallToBig:boxstate === 3}">
        <Button :icon="boxstate === 3 ? 'md-contract' : 'md-expand'" @click="boxstate = boxstate === 3 ? 0 :3" size="small" class="boxBtn"></Button>
        <div class="bigBox">
          <div class="smallBox">
            <Table border stripe :loading="mapLoading" :columns="mapColumns1" :data="mapData1" highlight-row>
            </Table>
          </div>
        </div>
      </Card>
      <!-- 查看详情-->
      <div class="detailsTable" :class="{detailsActive:detailsActive}">
        <detailsTable ref="detailsTable" @signOut="detailSignOut"></detailsTable>
      </div>
    </div>
  </div>
</template>
<script>
import detailsTable from './detailsTable'
import Map from './map'
import {
  // 表格列表
  list
} from '@/api/projectApproval'
export default {
  name: 'checkInfoMap',
  data () {
    return {
      map: {}, // 地图实例
      view: {}, // 地图视图实例
      /* 放大块的标识 */
      boxstate: 0,
      /* 表格数据 */
      mapColumns1: [
        {
          type: 'selection',
          width: 60,
          align: 'center'
        },
        {
          title: '序号',
          key: 'index',
          width: 100,
          align: 'center',
          render: (h, params) => {
            return h('span', params.index + 1 )
          }
        },
        {
          title: '任务名称',
          align: 'center',
          ellipsis: true,
          tooltip: true,
          minWidth: 200,
          // key: 'cl002'
          key: 'name'
        },
        {
          title: '原人类活动',
          align: 'center',
          ellipsis: true,
          tooltip: true,
          minWidth: 200,
          // key: 'cl010'
          key: 'name'
        },
        {
          title: '比对人类活动',
          align: 'center',
          ellipsis: true,
          tooltip: true,
          minWidth: 200,
          // key: 'cl009'
          key: 'name'
        },
        {
          title: '操作',
          key: '',
          width: 150,
          align: 'center',
          render: (h, params) => {
            return h('div', [
              h('Button', {
                props: {
                  type: 'primary',
                  size: 'small',
                  icon: 'md-eye'
                },
                style: {
                  marginRight: '5px'
                },
                on: {
                  click: () => {
                    this.detailsActive = true
                  }
                }
              }, '查看详情')
            ])
          }
        }
      ],
      mapData1: [],
      /* 加载 */
      mapLoading: false,
      /* 查看详情弹框状态 */
      detailsActive: false
    }
  },
  computed: {},
  props: {},
  components: {
    // 查看详情
    detailsTable,
    // 地图
    Map
  },
  watch: {},
  mounted () {
    this.getMapTableList()
  },
  methods: {
    /* 地图框放大缩小开关控制 */
    mapOpen () {
      this.boxstate = this.boxstate === 1 ? 0 : 1
    },
    /* 点击图层控制器打开地图框 */
    layerConClick () {
      this.boxstate = 1
    },
    /* 获取字段列表 */
    getMapTableList () {
      this.mapLoading = true
      list({
        data: {}
      }).then((res) => {
        if (res.data.code === '0000') {
          this.mapData1 = res.data.data.rows
          this.mapLoading = false
        }
      }).catch(() => {
        this.$Notice.error({ title: '服务器错误' })
      })
    },
    /* 查看详情页面退出 */
    detailSignOut (addActive) {
      this.detailsActive = addActive
    }
  }
}
</script>
<style scoped lang="scss">
  #tablet{
    position: relative;
    height: 100%;
    overflow: hidden;
    .queryBox {
      height: 50px;
      border-bottom: 1px solid #d3d3d3;
      margin: 0 15px 15px;
      position: relative;
      overflow: inherit;
      line-height: 50px;
      text-align: right;
      .title {
        float: left;
        font-size: 14px;
        i {
          vertical-align: middle;
          font-size: 20px;
          margin-right: 5px;
        }
      }
    }
    .map-box{
      width: 100%;
      height: calc(100% - 80px);
      padding: 0 15px 15px;
      position: relative;
      .topLeft-box {
        position: absolute;
        top: 0;
        left: 15px;
        width: calc(50% - 20px);
        height: calc(70% - 5px);
        border: 1px solid #ccc;
        /* 地图框样式 */
        #viewDiv{
          width: 100%;
          height: 100%;
        }
      }
      .topRight-box {
        position: absolute;
        top: 0;
        right: 15px;
        width: calc(50% - 20px);
        height: calc(70% - 5px);
        border: 1px solid #ccc;
        overflow: auto;
      }
      .bottom-box {
        position: absolute;
        bottom: 0;
        left: 15px;
        width: calc(100% - 30px);
        height: calc(30% - 5px);
        border: 1px solid #ccc;
        overflow: auto;
      }
      .sign-btn {
        position: absolute;
        right: 15px;
        bottom: 0px;
        z-index: 99;
      }
      /* box放大 */
      .activeBox, .smallToBig{
        position: absolute;
        left: 15px !important;
        top: 0 !important;
        width: calc(100% - 30px) !important;
        height: 100% !important;
        z-index: 999;
        border: none;
        transition: all 0.8s ease;
      }
      .smallToBig {
        background: #fff;
      }
      .boxBtn {
        position: absolute;
        top: 10px;
        right: 10px;
        z-index: 99;
      }
      .detailsTable {
        height: calc(100% + 65px);
        width: 100%;
        position: absolute;
        right: -120%;
        top: -65px;
        z-index: 1356;
        background: #fff;
        transition: all 0.8s ease;
        border-radius: 0 0 5px 5px;
        .closeBtn{
          position: absolute;
          right:10px;
          bottom:10px;
          z-index: 131531313;
        }
      }
      .detailsActive {
        right:0;
      }
      .ivu-card >>> .ivu-card-body {
        padding: 0 !important;
      }
    }
  }
</style>
