<template>
  <div id="tablet">
    <div class="queryBox">
      <QueryBox :formArr="queryFrom" @query="getQueryData" ref="getQuery"  style="width: calc(100% - 70px)"></QueryBox>
      <div class="queryBtns">
        <Button type="success" size="small" style="margin-right: 10px" class="btn-add" icon="md-add" @click.stop="openAddModal">新建</Button>
      </div>
    </div>
    <div class="content_box">
      <div>
        <Table border stripe :loading="loading" :columns="columns1" :data="data1" highlight-row :max-height="tableHeight">
        </Table>
        <div class="page_box">
          <Page :total="total" ref="pages" :page-size="pageSize" :current.sync="pageNum" @on-change="pageChange" show-total/>
        </div>
      </div>
    </div>
    <!-- 查看地图-->
    <div class="changeDetectTableMap" :class="{addActive:addActive}">
      <changeDetectTableMap ref="changeDetectTableMap" :seeId="taskId" @signOut="signOut"></changeDetectTableMap>
    </div>
    <Modal v-model="modal1"
           title="新增人类活动信息变化检测"
           :mask-closable="false"
           >
      <Form :model="addForm" :label-width="130" ref="addForm" :rules="addFormRules">
        <FormItem prop="name" label="任务名称">
          <Input v-model="addForm.name" placeholder="请填写任务名称，最少四个字符"></Input>
        </FormItem>
        <FormItem prop="image1Id" label="原人类活动数据">
          <Select v-model="addForm.image1Id">
          </Select>
        </FormItem>
        <FormItem prop="image2Id" label="比对人类活动数据">
          <Select v-model="addForm.image2Id">
          </Select>
        </FormItem>
        <FormItem prop="remark" label="说明">
          <Input v-model="addForm.remark" type="textarea"></Input>
        </FormItem>
      </Form>
      <div slot="footer">
        <Button @click="modal1=false">取消</Button>
        <Button type="primary" @click="submitAddForm" :loading="buttonLoading">确认</Button>
      </div>
    </Modal>
  </div>
</template>

<script>
import QueryBox from '@/components/publicQuery'
import changeDetectTableMap from './changeDetectTable_map'
import { getChangeList, getRemoteList2 } from '@/api/remoteSensing'
// import {
//   // 表格列表
//   list
// } from '@/api/projectApproval'
export default {
  name: 'changeDetectMap',
  data () {
    return {
      /* 表格高度 */
      tableHeight: Math.floor((window.innerHeight - 300 - 40) / 48) * 48 + 40,
      /* 查询框查询条件 */
      queryData: {},
      /* 表格数据 */
      columns1: [
        {
          type: 'selection',
          width: 60,
          align: 'center'
        },
        {
          title: '序号',
          key: 'index',
          width: 100,
          align: 'center',
          render: (h, params) => {
            return h('span', params.index + (this.pageNum - 1) * this.pageSize + 1)
          }
        },
        {
          title: '任务名称',
          align: 'center',
          ellipsis: true,
          tooltip: true,
          minWidth: 200,
          key: 'cl002'
          // key: 'name'
        },
        {
          title: '原人类活动',
          align: 'center',
          ellipsis: true,
          tooltip: true,
          minWidth: 200,
          key: 'cl010'
          // key: 'name'
        },
        {
          title: '比对人类活动',
          align: 'center',
          ellipsis: true,
          tooltip: true,
          minWidth: 200,
          key: 'cl009'
          // key: 'name'
        },
        {
          title: '操作',
          key: '',
          width: 150,
          align: 'center',
          render: (h, params) => {
            return h('div', [
              // 查看地图
              h('Button',
                {
                  props: {
                    type: 'success',
                    size: 'small'
                  },
                  style: {
                    marginRight: '5px',
                    background: 'rgb(16,186,204)',
                    borderColor: 'rgb(16,186,204)'
                  },
                  on: {
                    click: () => {
                      this.addActive = true
                    }
                  }
                },
                [h('i', {
                  class: 'iconfont icon-leimupinleifenleileibie',
                  style: {
                    fontSize: '12px',
                    marginRight: '4px'
                  }
                }),
                h('span', {
                  style: {
                    color: 'inherit',
                    cursor: 'pointer'
                  }
                }, '地图查看')]
              )
            ])
          }
        }
      ],
      data1: [],
      /* 总条数 */
      total: 0,
      pageNum: 1,
      pageSize: Math.floor((Math.floor((window.innerHeight - 300 - 40) / 48) * 48 + 40) / 48),
      /* 加载 */
      loading: false,
      /* 查看详情弹框状态 */
      addActive: false,
      /* 查看详情任务id */
      taskId: 0,
      /* 控制新增弹框的变量 */
      modal1: false,
      /* 添加新的人类活动变化检测任务的接口 */
      addForm: {
        name: '',
        image1Id: '',
        image2Id: '',
        remark: ''
      },
      // 添加新的人类活动变化检测任务--数据表单校验
      addFormRules: {
        name: [
          { required: true, message: '任务名称必须填写', trigger: 'blur' }
        ],
        image1Id: [
          { required: true, message: '原人类活动数据必须选择', trigger: 'change' }
        ],
        image2Id: [
          { required: true, message: '比对人类活动数据必须选择', trigger: 'change' }
        ]
      },
      // 添加新的人类活动变化检测任务--确定按钮loading
      buttonLoading: false
    }
  },
  components: {
    QueryBox,
    // 查看详情
    changeDetectTableMap
  },
  computed: {
    // 查询表单参数
    queryFrom: function () {
      return [
        {
          type: 1,
          field: 'cl002',
          title: '任务名称'
        },
        {
          type: 1,
          field: 'cl002',
          title: '原人类活动'
        },
        {
          type: 1,
          field: 'cl002',
          title: '比对人类活动'
        }
      ]
    }
  },
  created () {
    this.getTableList()
  },
  mounted () {
    // 窗口改变时table自适应
    window.addEventListener('resize', () => {
      this.tableHeight = Math.floor((window.innerHeight - 300 - 40) / 48) * 48 + 40
      this.pageSize = Math.floor((Math.floor((window.innerHeight - 300 - 40) / 48) * 48 + 40) / 48)
      this.getTableList()
    }, false)
  },
  methods: {
    /* 获取查询数据 */
    getQueryData (data) {
      this.queryData = data
      this.pageNum = 1
      this.getTableList()
    },
    /* 获取人类活动提取信息任务列表 */
    getRemoteList () {
      getRemoteList2({
        data: {}
      })
    },
    /* 获取字段列表 */
    getTableList () {
      this.loading = true
      getChangeList({
        data: {
          pageSize: this.pageSize,
          pageNum: this.pageNum,
          ...this.queryData
        }
      }).then((res) => {
        if (res.data.code === '0000') {
          // this.data1 = res.data.data.poList
          this.data1 = res.data.data.rows
          this.total = res.data.data.poSum
          this.loading = false
        }
      }).catch(() => {
        this.$Notice.error({ title: '服务器错误' })
      })
    },
    /* 分页跳转 */
    pageChange (pageNum) {
      this.pageNum = pageNum
      this.getTableList()
    },
    /* 查看问题点页面退出 */
    signOut (addActive) {
      this.addActive = addActive
    },
    /* 打开新增对比检测弹框 */
    openAddModal () {
      this.$refs.addForm.resetFields()
      this.addForm = {}
      this.modal1 = true
    },
    /* 保存添加新的人类活动变化检测任务 */
    submitAddForm () {
      this.buttonLoading = true
      this.$refs.addForm.validate(valid => {
        if (valid) {
          updateTask({
            data: {
              name: this.addForm.name,
              image1Id: this.addForm.image1Id,
              image2Id: this.addForm.image2Id,
              remark: this.addForm.remark
            }
          }).then(res => {
            if (res.data.code === '0000') {
              this.buttonLoading = false
              this.$Notice.success({ title: '人类活动变化检测任务添加成功' })
              // 清空--添加新的人类活动变化检测任务--表单及校验
              this.$refs.addForm.resetFields()
              this.addForm = {}
              this.modal1 = false
              this.getTableList()
            } else {
              this.buttonLoading = false
              this.$Notice.error({ title: '人类活动变化检测任务添加失败' })
            }
          })
        }
      })
    }
  }
}
</script>

<style lang='scss' scoped rel='stylesheet/scss'>
  #tablet{
    position: relative;
    .iconfont {
      margin-right: 4px;
    }
    .queryBox {
      height: 50px;
      border-bottom: 1px solid #d3d3d3;
      margin: 0 15px 15px;
      position: relative;
      overflow: inherit;
      .queryBtns {
        float: right;
        margin-top: 14px;
      }
    }
    .content_box {
      margin: 0 15px 0;
      .page_box{
        width: 100%;
        padding: 15px;
        text-align: right
      }
    }
    .changeDetectTableMap {
      height: 100%;
      width: 100%;
      position: absolute;
      right: -120%;
      top:0;
      z-index: 1356;
      background: #fff;
      transition: all 0.8s ease;
      border-radius: 0 0 5px 5px;
      .closeBtn{
        position: absolute;
        right:10px;
        bottom:10px;
        z-index: 131531313;
      }
    }
    .addActive{
      right:0;
    }
  }
</style>
